<template>
	<view>
		<view class="container">
			<view class="article-card">
				
				<view class="article-card-head">
					<view class="article-card-head-left">
						<!-- 发布者头像 -->
						<image :src="article.author_info.user_avatar"
							style="border-radius: 50%;width: 80rpx;height: 80rpx; margin-right: 10px;"></image>
						<view>
							<!-- 发布者 -->
							<view class="" style="color: #FC965E; font-weight: bold;">
								{{article.author_info.user_name}}
							</view>
							<!-- 发布时间 -->
							<view style="font-size: 20rpx;color: #999999;"> <uni-dateformat :date="article.time"
									:threshold="[60000,3600000,86400000]"></uni-dateformat> </view> 
						</view>
					</view>
					<view class="article-card-head-right">
						<!-- 文章分类 -->
						<text class="item-category">{{article.category}}</text>
					</view>
				</view>
				
				
				<view class="article-card-content">
					{{article.content}} <!-- 发布内容 -->
				</view>
				<view class="article-card-data">
					<!-- 浏览量 -->
					<view class="article-card-data-left">
						<!-- <text>浏览量</text>
						{{article.view_num}}
						<text>次</text> -->
					</view><!--例：浏览量 29次 -->
					<view class="article-card-data-right">
						<uni-icons type="chat" size='40rpx' @click="() => showKeyboard(true,'comment')"></uni-icons>
						<view style="margin-right: 20rpx;font-size: 22rpx;">{{article.comment_num}}</view>
						<uni-icons type="hand-up" size='40rpx' v-if="article.liked===false"
							@click="articleLikeClicked('add',article_id)" />
						<uni-icons color="rgb(41, 121, 255)"  type="hand-up-filled" size='40rpx' v-else
							@click="articleLikeClicked('sub',article_id)" />
						<view style="font-size: 22rpx;">{{article.like_num}}</view>
					</view>
				</view>
			</view>

			<view style="align-self: flex-start;padding: 30rpx 30rpx;">
				<text style="font-weight: bold;margin-right: 20rpx;">评论</text>
				<text style="font-weight: bold;">{{article.comment_num}}</text>
			</view>

			<!-- 评论区 -->
			<view class="comment-card" v-for="(item, index1) in article.comment" :key="index1">
				<!-- 头像组件无法修改样式，在组件外加一个view用来调整外边距 -->
				<view class="comment-card-head">
					<image :src="item.commenter_info.user_avatar" class="comment-card-head-avatar"
						style="border-radius: 50%;width: 60rpx;height: 60rpx;margin-right: 20rpx;"></image>
					<!-- 发布者 -->
					<view class="comment-card-head-publisher" style="align-self: center;font-size: 24rpx;">
						{{item.commenter_info.user_name}}
					</view>
				</view>

				<view class="comment-card-content">
					<view style="width: 80rpx;"></view>
					<text style="font-size: 25rpx;">{{item.content}}</text>
				</view>

				<!-- 评论数据，点赞功能 -->
				<view class="comment-card-data">
					<view style="font-size: 20rpx;color: #999999;display: flex;">
						<view style="width: 80rpx;"></view>
						<uni-dateformat :date="item.time" :threshold="[60000,3600000,86400000]"></uni-dateformat>
					</view>
					<view class="comment-card-data-right">
						<uni-icons type="hand-up" size='40rpx' v-if="item.liked===false"
							@click="commentLikeClicked('add',article_id,item._id)" />
						<uni-icons color="rgb(41, 121, 255)" type="hand-up-filled" size='40rpx' v-else
							@click="commentLikeClicked('sub',article_id,item._id)" />
						<text style="font-size: 22rpx;margin-right: 20rpx;">{{item.like_num}}</text>
						<uni-icons type="chat" size='40rpx'
							@click="() => showKeyboard(true,'reply',item._id,item.commenter_info.user_name,item.commenter_info._id)" />
					</view>
					
				</view>
				
				<!-- <view v-if="item.isViewAll === false">
					<view class="view-all" v-if="item.reply_num > 0" @click="viewAll(article_index,index1,item._id)">
						<text>展开{{item.reply_num}}条回复</text>
					</view>
				</view> -->
				
				
				<!-- 评论回复区 -->
				<!-- <view class="reply" v-for="(reply,index2) in item.reply" :key="index2"> -->
					<!-- 头像组件无法修改样式，在组件外加一个view用来调整外边距 -->
					<!-- <view class="reply-head"> -->
						<!-- 发布者头像 -->
						<!-- <image :src="reply.replyer_info.user_avatar" -->
							<!-- style="border-radius: 50%;width: 60rpx;height: 60rpx;margin-right: 20rpx;"></image> -->
						<!-- 发布者 -->
						<!-- <view class="comment-publisher" style="align-self: center;font-size: 24rpx;"> -->
							<!-- {{reply.replyer_info.user_name}} -->
						<!-- </view> -->
						<!-- <uni-icons type="forward" size="20rpx" style="margin: 0 20rpx;" /> -->
						<!-- <view style="align-self: center;font-size: 24rpx; ">{{reply.replyer_to_user_name}}</view> -->
					<!-- </view> -->
					<!-- 发布内容 -->
					<!-- <view class="reply-content"> -->
						<!-- {{reply.content_reply}} -->
					<!-- </view> -->

					<!-- <view class="comment-card-data">
						<view class="comment-card-data-left">
							<view style="width: 80rpx;"></view>
							<uni-dateformat :date="reply.time" :threshold="[60000,3600000,86400000]"></uni-dateformat>
						</view>
						<view class="comment-card-data-right">
							<uni-icons type="hand-up" size='40rpx' v-if="reply.liked===false"
								@click="replyLikeClicked('add',article_id,item._id,reply._id)" />
							<uni-icons color="rgb(41, 121, 255)" type="hand-up-filled" size='40rpx' v-else
								@click="replyLikeClicked('sub',article_id,item._id,reply._id)" />
							<text style="font-size: 22rpx;">{{reply.like_num}}</text> -->
							<!-- 评论按钮 -->
							<!-- <uni-icons type="chat" size='40rpx'
								@click="() => showKeyboard(true,'reply',item._id,reply.commenter_info.user_name,itme.commenter_info._id)"></uni-icons> -->
					<!-- 	</view>

					</view>
					
				
				</view> -->
			
			</view>

			<!-- 键盘区 -->
			<!-- 根据变量控制输入框的显示和隐藏 -->
			<view class="input-wrap" v-show="isShowKeyboard">
				<view style="margin: 40rpx;">
					<image :src="avatar()" style="border-radius: 50%;width: 60rpx;height: 60rpx;"></image>
				</view>
				<view style="display: flex;align-items: center; background-color: #F5F7FA;border-radius: 20rpx;">
					<textarea @blur="showKeyboard(false)" v-model="message" :autoHeight="true" placeholder="请输入评论内容"
						:focus='isShowKeyboard' :show-confirm-bar="false" :fixed="true" cursor-spacing='40rpx'
						style="margin: 20rpx;width:450rpx;"></textarea>
					<text @click="sendMessage" style="margin: 20rpx 30rpx;align-self: flex-end;">完成</text>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js';
	export default {
		data() {
			return {
				isShowKeyboard: false, //控制输入框显示
				message: '', //评论区输入框内容
				article_id: '',
				article_index: '',
				message_type: String,
				comment_id: String,
				replyer_to_user: String,
				replyer_to_user_id: String,
			}
		},
		onLoad(options) {
			uni.showLoading({
				title: '加载中...'
			});
			this.isShowKeyboard = JSON.parse(options.isShowKeyboard);
			this.article_id = options.article_id;
			this.message_type = options.message_type;
			this.article_index = store.state.article.findIndex(item => item._id === options.article_id);
			if (!("comment" in store.state.article[this.article_index])) {
				store.dispatch('getComment', {
					index: this.article_index,
					article_id: options.article_id
				});
			};
			uni.hideLoading();
		},
		computed: {
			article() {
				return store.state.article.find(item => item._id === this.article_id);
			}
		},
		methods: {
			checkLogin() {
				// 检查是否注册过
				const token = uni.getStorageSync('token');
				if (!token) {
					uni.showModal({
						title: '提示',
						content: '未登录，请您登录',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
								uni.switchTab({
									url: '/pages/my/my'
								});
							} else if (res.cancel) {
								console.log('用户点击取消');
								uni.switchTab({
									url: '/pages/home/home'
								});
							}
						}
					});
					return false
			
				} else {
					return true
				}
			},
			showKeyboard(flag, message_type, comment_id, replyer_to_user, replyer_to_user_id) {
				if(!this.checkLogin()){
					return ;
				} else {
					this.isShowKeyboard = flag;
					this.message_type = message_type;
					this.comment_id = comment_id;
					this.replyer_to_user = replyer_to_user;
					this.replyer_to_user_id = replyer_to_user_id;
				}
			},
			
			sendMessage() {
				const that = this;
				if (!this.message) {
					uni.showToast({
						title: "输入不能为空"
					});
					return;
				};
				uni.showLoading({
					title: '敏感内容检测中...'
				});
				if (this.message_type === "comment") {
					uniCloud.callFunction({
						name: 'uploadComment',
						data: {
							article_id: this.article_id,
							content: this.message,
							token: uni.getStorageSync('token'),
							time: Date.now(),
						}
					}).then((res) => {
						uni.hideLoading();
						console.log("detail204:",res.result);
						if (res.result) {
							uni.showToast({
								title: '内容敏感',
								icon: 'error'
							});
						} else {
							store.dispatch('getComment', {
								index: that.article_index,
								article_id: that.article_id,
							});
							that.message = '';
						};
					});
				};
				if (this.message_type === "reply") {
					uniCloud.callFunction({
						name: 'uploadReply',
						data: {
							article_id: this.article_id,
							content_reply: this.message,
							replyer_name: uni.getStorageSync('user_info').user_name,
							token: uni.getStorageSync('token'),
							time: Date.now(),
							comment_id: this.comment_id,
							replyer_to_user_name: this.replyer_to_user,
							replyer_to_user_id: this.replyer_to_user_id,
						}
					}).then((res) => {
						console.log(that.comment_id);
						uni.hideLoading();
						if (res.result) {
							uni.showToast({
								title: '内容敏感',
								icon: 'error'
							});
						} else {
							console.log(that.comment_id);
							store.dispatch('getReply', {
								article_index: that.article_index,
								comment_id: that.comment_id
							});
						};
					});
				}
			},

			articleLikeClicked(api, article_id) {
				if(!this.checkLogin()){
					return ;
				} else {
					const liked = api === "add";
					store.commit('tempSetLiked', {
						liked,
						aritcle_id
					});
					uniCloud.callFunction({
						name: 'updateLike',
						data: {
							api: "article",
							liked,
							token: uni.getStorageSync('token'),
							article_id
						}
					});
				}
			},
			commentLikeClicked(api, article_id, comment_id) {
				if(!this.checkLogin()){
					return ;
				} else {
					const liked = (api === "add");
					store.commit('tempSetLiked', {
						liked,
						article_id,
						comment_id
					});
					uniCloud.callFunction({
						name: 'updateLike',
						data: {
							api: "comment",
							liked,
							token: uni.getStorageSync('token'),
							comment_id
						}
					});
				};
			},
			// replyLikeClicked(api, article_id, comment_id, reply_id) {
			// 	const liked = (api === "add");
			// 	store.commit('tempSetLiked', {
			// 		liked,
			// 		article_id,
			// 		comment_id,
			// 		reply_id
			// 	});
			// 	uniCloud.callFunction({
			// 		name: 'updateLike',
			// 		data: {
			// 			api: "reply",
			// 			liked,
			// 			token: uni.getStorageSync('token'),
			// 			reply_id
			// 		}
			// 	});
			// },
			avatar() {
				return uni.getStorageSync('user_info').user_avatar
			},
			// viewAll(article_index,comment_index,comment_id) {
			// 	store.dispatch('getReply',{article_index,comment_id});
			// 	store.commit('setIsViewAll',{article_index,comment_index});
			// }
		}
	}
</script>

<style>
	.container {
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 20rpx 0;
	}

	.article-card {
		width: 90%;
		border-radius: 20rpx;
		background-color: white;
		padding: 30rpx;
		display: flex;
		flex-direction: column;
		margin-top: 40rpx;
	}

	.article-card-head {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 40rpx;
	}
	.article-card-head-left {
		display: flex;
	}
	
	.item-category {
		margin-left: 10px;
		font-size: 14px;
		color: rgb(193, 193, 193);
	}

	.article-card-content {
		width: 100%;
		margin-bottom: 30rpx;
	}

	.article-card-data {
		display: flex;
		justify-content: space-between;
		width: 100%;

	}

	.article-card-data-left {
		font-size: 22rpx;
		display: flex;
		align-items: center;
	}

	.article-card-data-right {
		weight: 100rpx;
		display: flex;
		align-items: center;

	}

	.comment-card {
		width: 90%;
		border-radius: 20rpx;
		background-color: white;
		padding: 30rpx;
		display: flex;
		flex-direction: column;
		margin-top: 40rpx;
	}
	.comment-card-head {
		display: flex;
	}
	

	.content {
		width: 100%;
	}

	.comment-card-content {
		display: flex;
		justify-self: start;
		width: 100%;
		font-weight: bold;
		margin-bottom: 20rpx;
	}

	.comment-card-data {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		margin-bottom: 20rpx;
	}

	.comment-card-data-left {
		font-size: 20rpx;
		color: #999999;
		display: flex;
	}

	.comment-card-data-right {
		weight: 100rpx;
		display: flex;
		align-items: center;
	}

	.view-all {
		background-color: #f8f8f8;
		border-radius: 5rpx;
		width: fit-content;
		padding: 10rpx 20rpx;
		margin-left: 80rpx;
	}
	
	.view-all text {
		font-size: 22rpx;
		font-weight: bold;
		color: rgb(41, 121, 255);
	}

	.data {
		display: flex;
		justify-content: space-between;
		width: 100%;
	}

	.data-right {
		weight: 100rpx;
		display: flex;
		align-items: center;
	}

	.input-wrap {
		display: flex;
		align-items: center;
		width: 100%;
		background-color: #FFFFFF;
		position: fixed;
		bottom: 0;
	}

	/* 回复版块样式 */
	.reply {
		background-color: #f8f8f8;
		width: 100%;
		border-radius: 5rpx;
		padding: 20rpx 20rpx 10rpx;
	}

	.reply-head {
		display: flex;
		align-items: center;
		font-weight: bold;
	}

	.reply-content {
		display: flex;
		justify-self: start;
		width: 100%;
		margin-left: 80rpx;
		font-weight: bold;
	}
</style>